* {
  margin: 0;
  padding: 0;
}

body {
  background: #f7c115;
  background-image: url('../images/undraw_in_sync_xwsa-orange.svg');
  background-repeat: no-repeat;
  background-size: 75vh;
  background-position: center;
}

#main-content {
  margin-top: 52px;
}

form input {
  font-size: 16px;
}

input::-webkit-input-placeholder {
  font-size: 16px;
}

.ui .item {
  padding: 5px 10px !important;
}